.loader-wrapper {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: black;
    display: grid;
    place-items: center;
}

.loader {
    border-left: 0.5px solid gainsboro;
    position: absolute;
    left: 50%;
    margin-left: -3px;
    height: 100%;
    animation: slideDown 2.2s ease-in-out forwards;
}

@keyframes slideDown {
    from {
        transform: translate(0, -100%);
    }
    to {
        transform: translate(0, 75%);;
    }
}

.row {
    width: 100%;
    height: 100%;
    position: fixed;
}

.blocks {
    float: left;
    width: 20%;
    height: 100%;
    background-color: aqua;
}

.blocksgroup1 {
    background-color: darkblue;
    position: relative;
    top: 100%;
    animation: slideDownColumn 0.6s ease-in-out forwards;
    animation-delay: 1.4s;
}

.blocksgroup2 {
    background-color: darkgoldenrod;
    position: relative;
    top: -100%;
    animation: slideUpColumn 0.7s ease-in-out forwards;
    animation-delay: 1.8s;
}
.blocksgroup22 {
    background-color: darkgrey;
    position: relative;
    top: -100%;
    animation: slideUpColumn 0.7s ease-in-out forwards;
    animation-delay: 2s;
}

.blocksgroup3 {
    background-color: darkmagenta;
    position: relative;
    top: 100%;
    animation: slideDownColumn 0.8s ease-in-out forwards;
    animation-delay: 2.3s;
}
.blocksgroup33 {
    background-color: darkred;
    position: relative;
    top: 100%;
    animation: slideDownColumn 0.8s ease-in-out forwards;
    animation-delay: 2.1s;
}

@keyframes slideDownColumn {
    from{
        transform: translate(0,0%);
    }
    to{
        transform: translate(0, -100%);
    }
}

@keyframes slideUpColumn {
    from{
        transform: translate(0,0%);
    }
    to{
        transform: translate(0, 100%);
    }
}